//All about the colors
$dark-blue : #18152B;
$primary-white : #eeeeff;
$primary-dark : #111111;
$primary-blue : #7464FB;
$empty-dark : #302b53;
$primary-gray : #ffffff38;

//All about the fonts
@font-face {
  font-family: MetropolisBold;
  src: url(../fonts/MetropolisBold.otf);
}
@font-face {
  font-family: MetropolisRegular;
  src: url(../fonts/MetropolisRegular.otf);
}
@font-face {
  font-family: MetropolisThin;
  src: url(../fonts/MetropolisThin.otf);
}
@font-face {
  font-family: PlatNomor;
  src: url(../fonts/PlatNomor.otf);
}


// All about the mixins
@mixin flex-container($direction,$align,$justify,$wrap){
    display: flex;
    flex-wrap: $wrap;
    flex-direction: $direction;
    align-items: $align;
    justify-content: $justify;
}
@mixin button-component($bg-color,$text-color){
    background-color : $bg-color;
    color : $text-color;
}
@mixin container-width($cont-width){
    width: 100%;
    max-width: $cont-width;
    margin: 0 auto;
    padding: 0px 20px;
}

// Different Classes
.container-1000{
    @include container-width(1000px);
}
.container-1200{
    @include container-width(1200px);
}
.container-1400{
    @include container-width(1400px);
}

// Stuffs related to the fluid typography from the CSS tricks
@function strip-unit($value) {
    @return $value / ($value * 0 + 1);
  }
  
@mixin fluid-type($min-vw, $max-vw, $min-font-size, $max-font-size) {
    $u1: unit($min-vw);
    $u2: unit($max-vw);
    $u3: unit($min-font-size);
    $u4: unit($max-font-size);
  
    @if $u1 == $u2 and $u1 == $u3 and $u1 == $u4 {
      & {
  
        font-size: $min-font-size;
        @media screen and (min-width: $min-vw) {
          font-size: calc(#{$min-font-size} + #{strip-unit($max-font-size - $min-font-size)} * ((100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)}));
        }
  
        @media screen and (min-width: $max-vw) {
          font-size: $max-font-size;
        }
      }
    }
  }
// All related to svg
svg{
  cursor: pointer;
}

.filler-line{
  width: 100%;
  height: 1px;
  background-color: $primary-gray;
  margin: 10px 0;
}
h1{
  font-family: "PlatNomor";
  @include fluid-type(1200px, 320px, 35px, 70px);
}
h2{
  font-family: "MetropolisBold";
}
h3,h4,h5,h6,p,label,input,button,select,option{
  font-family: "MetropolisThin";
  @include fluid-type(1200px, 320px, 17px, 19px);
}
input{
  color: $primary-white;
}
.btn{
  padding: 15px 60px;
  display: inline-block;
  cursor: pointer;
  &-configure{
    border: 2px solid $primary-blue;
  }
}

