}

//Styling the Main Section
.main__section{
  @include flex-container(row, center, center, wrap);
  padding: 70px;
  // background-color: pink;
  >div{
    @include flex-container(column, center, center, wrap);
  }
  &--icons{
    @include flex-container(row, center, center, no-wrap);
    @media screen and (max-width:365px){
      flex-direction: column;
    }
    &>div{
      cursor: pointer;
      margin: 10px;
      >div{
        padding: 15px;
        background-color: $empty-dark;
      }
    }
    .btn-restart{
      padding: 15px;
      background-color: $empty-dark;
    }
  }
}


//All related to the configure page..
.configure__section{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  @include flex-container(row, center, center, wrap);
  background-color: $dark-blue;
  min-height: 100vh;
  height: 100%;

  &--box{
    padding: 45px;
    background-color: $empty-dark;
    width: 50vw;
    @media screen and (max-width:1200px){
      width: 60vw;
    }
    @media screen and (max-width:950px){
      width: 70vw;
    }
    @media screen and (max-width:700px){
      width: 80vw;
    }
    @media screen and (max-width:580px){
      width: 95vw;
    }
    &-upper{
      @include flex-container(row, center, space-between, no-wrap);
    }
  }
  &--form{
    label{
      padding:15px 0;
    }
    &-btn{
      text-align: center;
      margin-top: 40px;
    }
  }
}

// All related to the Input field
label{
  display: block;
}
input,select{
  width: 100%;
  background-color: transparent;
  outline: none;
  border: 1px solid $primary-gray;
  padding: 15px;
  color: $primary-white;
}

option{
  color: $primary-white;
  background-color: $dark-blue;
  margin: 5px 0;
  padding: 20px;
  border: none;
}
.submit-btn{
  background-color: $primary-blue;
  color: $primary-white;
  font-family: MetropolisBold;
}


// ALl about the countdownsvg
.base__timer{
  // background-color: pink;
  position: relative;
  height: 300px;
  width: 300px;
  &--svg{
    fill: none;
    stroke: none;
    /* Flips the svg and makes the animation to move left-to-right */
    transform: scaleX(-1);
  }
  &--path-elapsed{
    stroke-width: 5px;
    stroke: $primary-blue;
    fill: none;
  }
 &--label{
  position: absolute;
  // background-color: pink;
  width: 300px;
  height: 300px;
  top: 0;
  @include flex-container(column, center, center, no-wrap);
  h1{
    margin:30px 0px 30px;
    text-align: center;
  }
  p{
    color: $primary-white;
    text-align: center;
  }
 }
}